import React, { useEffect, useState } from "react";
import PropTypes from "prop-types";
import { format } from "d3-format";
import { timeFormat } from "d3-time-format";
import {
  discontinuousTimeScaleProviderBuilder,
  Chart,
  ChartCanvas,
  BarSeries,
  CandlestickSeries,
  lastVisibleItemBasedZoomAnchor,
  XAxis,
  YAxis,
  CrossHairCursor,
  EdgeIndicator,
  MouseCoordinateX,
  MouseCoordinateY,
  ZoomButtons,
  withDeviceRatio,
  withSize
} from "react-financial-charts";
//import { useMarketData } from "./useMarketData";
import OHLCTooltip from "./OHLCTooltip";
import { tsvParse } from "d3-dsv";
import { timeParse } from "d3-time-format";
import { useRef } from "react";


const axisStyles = {
  strokeStyle: "#383E55", // Color.GRAY
  strokeWidth: 2,
  tickLabelFill: "#9EAAC7", // Color.LIGHT_GRAY
  tickStrokeStyle: "#383E55",
  gridLinesStrokeStyle: "rgba(56, 62, 85, 0.5)" // Color.GRAY w Opacity
};

const coordinateStyles = {
  fill: "#383E55",
  textFill: "#FFFFFF"
};

const zoomButtonStyles = {
  fill: "#383E55",
  fillOpacity: 0.75,
  strokeWidth: 0,
  textFill: "#9EAAC7"
};

const crossHairStyles = {
  strokeStyle: "#9EAAC7"
};

// Default green/red colors for candlesticks
const openCloseColor = (d) => (d.close > d.open ? "#26a69a" : "#ef5350");

// yExtentsCalculator: used from updating price series
// https://github.com/react-financial/react-financial-charts/blob/main/packages/stories/src/features/updating/BasicLineSeries.tsx#L55
const yExtentsCalculator = ({ plotData }) => {
  let min;
  let max;
  for (const { low, high } of plotData) {
    if (min === undefined) min = low;
    if (max === undefined) max = high;
    if (low !== undefined && min > low) min = low;
    if (high !== undefined && max < high) max = high;
  }
  if (min === undefined) min = 0;
  if (max === undefined) max = 0;

  const padding = (max - min) * 0.1;
  return [min - padding, max + padding * 2];
};

// https://www.joshwcomeau.com/snippets/react-hooks/use-interval/
const useInterval = (callback, delay) => {
  const intervalRef = useRef(null);
  const savedCallback = useRef(callback);
  useEffect(() => {
    savedCallback.current = callback;
  }, [callback]);
  useEffect(() => {
    const tick = () => savedCallback.current();
    if (typeof delay === "number") {
      intervalRef.current = window.setInterval(tick, delay);
      return () => window.clearInterval(intervalRef.current);
    }
  }, [delay]);
  return intervalRef;
};


const FinancialChart = ({
  dateTimeFormat,
  height,
  margin,
  priceDisplayFormat,
  ratio,
  width
}) => {
  const [dataSet,setDataset] = useState('MINUTES');

  //const { data: initialData, loaded } = useMarketData(dataSet,false);
  const [initialData,setInitialData] = useState([]);
  const [loaded,setLoaded] = useState(Boolean(initialData));
  const [updating,setUpdating] = useState(false);
  const [length,setLength] = useState(50);
  const [data1,setData1] = useState(initialData); //for rendering live data
  const [crossHair,setCrossHair] = useState(false);

  useInterval(() => {
    if (initialData && updating) setLength(length + 1);
  }, 1000);

  useEffect(()=>{
    setData1(updating? initialData?.slice(0, length + 1) : initialData);
    console.log(data1);
  },[updating,length])
  
  useEffect(()=>{
    fetch(
      `./data/${dataSet}.tsv`
    )
      .then((response) => response.text())
      .then((data) => tsvParse(data, parseData()))
      .then((data) => {
        console.log('in useEffect',data)
        setInitialData(data);
        setLoaded(Boolean(data));
      });
  },[dataSet])

const parseDate = timeParse("%Y-%m-%d");
const parseData = () => (d) => {
  const date = parseDate(d.date);
  date ? (d.date = new Date(date)) : (d.date = new Date(Number(d.date)));
  for (const key in d) {
    if (key !== "date" && Object.prototype.hasOwnProperty.call(d, key)) {
      d[key] = +d[key];
    }
  }
  return d;
};

  

 console.log('Initial data length :',initialData?.length);



  const [resetCount, setResetCount] = useState(0);
  if (!loaded || !height || !ratio || !width) return <h1>Loading...!</h1>;
  const timeDisplayFormat = timeFormat(dateTimeFormat);
  const xScaleProvider = discontinuousTimeScaleProviderBuilder().inputDateAccessor(
    (d) => { 
      //console.log(d.date);
      return d.date;
    }
  );
  const { data, xScale, xAccessor, displayXAccessor } = xScaleProvider(
    updating?data1?data1:[]:initialData?initialData:[]
  );
  //console.log('xscaleProvider');
  //console.log(xScaleProvider(initialData));

  const min = xAccessor(data[Math.max(0, data.length - parseInt(width / 5))]);
  const max = xAccessor(data[data.length - 1]);
  const xExtents = [min, max + 1];

  const gridHeight = height - margin.top - margin.bottom;
  const barChartHeight = gridHeight / 5;
  const barChartOrigin = (_, h) => [0, h - barChartHeight];
  
  console.log('rendered')
  // ChartCanvas is drawn from top to bottom
  return (
  <>
  <div style={{display:'flex',columnGap:'50px'}}>
    <label style={{display:'flex', alignContent:'center',alignItems:'center',columnGap:'10px'}}>
      Stock:
      <select 
        value={dataSet} 
        onChange={(e)=>{console.log(e.target.value); setDataset(e.target.value)}}
        >
        <option value="MINUTES">NSE - large</option>
        <option value="MINUTES1">ZEN</option>
        <option value="MINUTES2">TCS</option>
        <option value="MINUTES3">ONGC</option>
        <option value="MINUTES4">ACC</option>
        <option value="MINUTES5">IOB</option>
        <option value="MINUTES6">GOLDTECH</option>
        <option value="MINUTES7">IFX</option>
        <option value="">None</option>
      </select>
    </label>
    <p>Type: </p>
    <label style={{display:'flex', alignContent:'center',alignItems:'center'}}>
        <input
          type="radio"
          checked={updating === true}
          onChange={()=>setUpdating(true)}
        />
        Live
    </label>
    <label style={{display:'flex', alignContent:'center',alignItems:'center'}}>
        <input
          type="radio"
          checked={updating === false}
          onChange={()=>setUpdating(false)}
        />
        Historic
    </label>
    <label style={{display:'flex', alignContent:'center',alignItems:'center'}}>
        <input
          type="checkbox"
          checked={crossHair}
          onChange={()=>setCrossHair(!crossHair)}
        />
        Crosshair
    </label>
  </div>
    <ChartCanvas
      height={height}
      ratio={ratio}
      width={width}
      margin={margin}
      seriesName={`Chart ${resetCount}`}
      data={data}
      xScale={xScale}
      xAccessor={xAccessor}
      displayXAccessor={displayXAccessor}
      xExtents={xExtents}
      zoomAnchor={lastVisibleItemBasedZoomAnchor}
    >
      {/* Volume Chart */}
      <Chart
        id={1}
        height={barChartHeight}
        origin={barChartOrigin}
        yExtents={(d) => d.volume}
      >
        <BarSeries
          fillStyle={(d) =>
            d.close > d.open
              ? "rgba(38, 166, 154, 0.3)"
              : "rgba(239, 83, 80, 0.3)"
          }
          yAccessor={(d) => d.volume}
        />
      </Chart>

      {/* Price Chart */}
      <Chart id={2} yExtentsCalculator={yExtentsCalculator}>
        <XAxis {...axisStyles} showGridLines />
        <MouseCoordinateX
          displayFormat={timeDisplayFormat}
          {...coordinateStyles}
        />
        <YAxis {...axisStyles} showGridLines />
        <MouseCoordinateY
          rectWidth={margin.right}
          displayFormat={priceDisplayFormat}
          {...coordinateStyles}
        />

        {/* YAxis close price highlight */}
        <EdgeIndicator
          itemType="last"
          rectWidth={margin.right}
          fill={openCloseColor}
          lineStroke={openCloseColor}
          displayFormat={priceDisplayFormat}
          yAccessor={(d) => d.close}
        />
        <CandlestickSeries />
        <OHLCTooltip
          origin={[8, 16]}
          textFill={openCloseColor}
          className="react-no-select"
        />
        <ZoomButtons
          onReset={() => setResetCount(resetCount + 1)}
          {...zoomButtonStyles}
        />
      </Chart>
      {crossHair?<CrossHairCursor {...crossHairStyles} />:null}
    </ChartCanvas>
  </>
  );
};

FinancialChart.propTypes = {
  dateTimeFormat: PropTypes.string,
  height: PropTypes.number,
  margin: PropTypes.object,
  priceDisplayFormat: PropTypes.func,
  ratio: PropTypes.number,
  width: PropTypes.number
};

FinancialChart.defaultProps = {
  dateTimeFormat: "%d %b '%y \xa0 %H:%M",
  height: 0,
  margin: { left: 20, right: 48, top: 10, bottom: 24 },
  priceDisplayFormat: format(".2f"),
  ratio: 0,
  width: 0
};

export const PriceChart = withSize({ style: { minHeight: 600 } })(
  withDeviceRatio()(FinancialChart)
);
